/*路由跳转动画*/
.slide-to-left-enter-active,
.slide-to-right-leave-active,
.slide-to-left-leave-active,
.slide-to-right-enter-active {
  transition: all 0.3s ease-out;
}
/*进入的组件什么也不做，只是保持透明，准备替代离开的组件。*/
.slide-to-left-enter,
.slide-to-left-enter-active {
  opacity: 0;
}
/*离开的元素向左过渡位移离开，并逐渐透明。*/
.slide-to-left-leave-active {
  -webkit-transform: translate(80%);
          transform: translate(80%);
  opacity: 0;
}
.slide-to-right-enter,
.slide-to-right-enter-active {
  opacity: 0;
}
.slide-to-right-leave-active {
  -webkit-transform: translate(-80%);
          transform: translate(-80%);
  opacity: 0;
/*仍有bug，当向右滑动时，页面会自动变宽。
    用给父元素声明overflow hidden解决了。*/
}
body {
  background-color: #f2f2f2;
}
#app .head-part {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 20;
}
#app .tab-group {
  width: 100%;
  max-width: 100%;
  margin-bottom: 10px;
  background-color: #f5f5f5;
  box-shadow: 0 1px 2px rgba(0,0,0,0.15);
  display: -wbekit-flex;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
/*flex-flow nowrap row*/
  z-index: 1;
  overflow: hidden;
}
#app .tab-group .tab {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: block;
  font-size: 0.875rem;
  font-weight: 700;
  text-align: center;
  line-height: 40px;
  color: #5d5d5d;
  position: relative;
}
#app .tab-group .active-tab {
  color: #ff8200;
  font-size: 0.9275rem;
}
#app .tab-group .active-tab:before {
  content: ' ';
  width: 100%;
  height: 2px;
  background-color: #ff8200;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
}
#app .main-body {
  padding: 94px 0 0 0;
  position: relative;
  overflow: hidden;
}
#app .addToHomeTip {
  position: fixed;
  bottom: 10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 0.9275rem;
  background-color: #f8f8f8;
  text-align: center;
  line-height: 50px;
  padding: 0 10px 0 60px;
  border: 1px solid #d2caca;
  border-radius: 5px;
  z-index: 21;
  overflow: visible;
}
#app .addToHomeTip .close-add-tip-icon {
  width: 18px;
  line-height: 18px;
  font-size: 12px;
  position: absolute;
  top: 0;
  right: 0;
}
#app .addToHomeTip .icon-block {
  width: 40px;
  height: 40px;
  background: url("../static/img/favicon-min.jpg") 0 0/contain no-repeat;
  border-radius: 5px;
  position: absolute;
  top: 5px;
  left: 10px;
}
#app .addToHomeTip:after {
  content: '';
  width: 0;
  height: 0;
  border: solid rgba(0,0,0,0);
  border-width: 10px;
  border-top-color: #d2caca;
  position: absolute;
  bottom: -21px;
  left: 50%;
  -webkit-transform: translateX(-11px);
          transform: translateX(-11px);
}


